.header {
	position: relative;
	&:before {
		content: '';
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: $gray2;
		z-index: -3; }
	&-top {
		padding: 24px 0;
		.container {
			@include flex(row, between, center, wrap); } }
	&-bottom {
		.container {
			box-shadow: $tab-shadow;
			border-radius: 4px 0 0 4px;
			background: white;
			@include flex(row, between, null, wrap); } }
	.logo {
		&__title {
			font: 800 20px/1 'Exo', sans-serif;
			color: $dark;
			span {
				color: $yellow; } }
		&__text {
			font-size: 12px;
			margin: 9px 0 0 0;
			letter-spacing: 0.01em;
			color: $gray; } }
	.top-nav {
		@include flex(row, between, center, wrap);
		li {
			margin: 0 10px; }
		a {

			color: $dark;
			text-transform: uppercase;
			font: 700 14px/1 'Exo', sans-serif; } }
	.price-link {
		@include flex(row, null, center, wrap);
		font: 500 14px/1.2 'Exo', sans-serif;
		color: $dark;
		text-decoration: underline;
		span {
			margin: 0 0 0 10px; } }
	.measure-link {
		background: white;
		color: $dark;
		font: 700 14px/1.2 'Exo', sans-serif;
		@include flex(row, null, center, wrap);
		padding: 10px 16px;
		text-transform: uppercase;
		border-radius: 4px;
		border: 1px solid rgba(51, 51, 51, 0.2);
		box-sizing: border-box;
		box-shadow: 0px 4px 20px rgba(51, 51, 51, 0.1);
		margin: 0 20px 0 0;
		span {
			margin: 0 0 0 10px; } }
	.block {
		text-align: right;
		&__title {
			color: $gray;
			font-size: 12px;
			letter-spacing: 0.01em;
			margin: 0 0 1px 0; } }
	.tels {
		margin: 0 0 1px 0;
		li {
			position: relative;
			&:before {
				content: '';
				display: block;
				width: 20px;
				height: 20px;
				@include bg(null, null, 'icons/phone.svg', no-repeat, center, center, contain);
				position: absolute;
				left: -30px;
				top: 50%;
				transform: translate(0, -50%); } }
		a {
			font: 800 24px/1 'Exo', sans-serif;
			letter-spacing: 0.01em;
			color: $dark; } }
	.callback-link {
		letter-spacing: 0.01em;
		color: $gray;
		text-decoration: underline;
		font: 400 14px/1 'Exo', sans-serif; }
	.menu {
		padding: 0;
		@include flex(row, null, center, wrap);
		li {
			list-style-type: none; }
		a {
			padding: 20px 30px;
			text-transform: uppercase;
			color: $dark;
			font: 800 16px/1 'Exo', sans-serif; } } }


