.text-container {
	line-height: 1.5;
	h1,h2,h3,h4,h5,h6 {
		text-transform: uppercase;
		font-weight: 800;
		font-family: 'Exo', sans-serif;
		margin-bottom: 15px;
		span {
			color: $yellow; } }
	h1 {
		font-size: 40px; }
	h2 {
		font-size: 36px; }
	h3 {
		font-size: 30px; }
	h4 {
		font-size: 20px; }
	h5 {
		font-size: 18px; }
	h6 {
		font-size: 16px; }
	p {
		font-size: 16px;
		margin-bottom: 15px;
		@include mq($max-width: 1200px) {
			font-size: 14px; } }
	ul {
		padding: 0 0 0 20px;
		margin: 0 0 20px 0;
		li {
			list-style-image: url('../img/icons/list-check.svg'); } }
	ol {
		margin: 0 0 20px 0;
		li {} } }
