.catalog-banner {
	background: $yellow-gradient;
	padding: 80px 0 50px 0;
	position: relative;
	.block-title {
		margin: 0 0 15px 0;
		text-align: center; }
	.container {
		position: relative;
		z-index: 2; }
	.inner {
		max-width: 912px;
		width: 100%;
		margin: 0 auto;
		@include flex(row, between, start, wrap); }
	.form-wrap {
		display: block;
		max-width: 315px;
		width: 100%;
		padding: 30px 0 0 0; }
	.form {
		max-width: 270px;
		width: 100%;
		&-action {
			margin: 15px 0; }
		&-item {
			width: 100%; }
		&__input {
			width: 100%; }
		&__btn {
			width: 100%; } }
	.mocup {
		max-width: 564px;
		width: 100%;
		position: relative;
		z-index: 2; }
	.arrow {
		position: absolute;
		right: 0;
		bottom: 50px;
		z-index: 2;
		transform: translate(0, -100%); }
	.circles {
		position: absolute;
		left: 0;
		top: 0; }
	.circles2 {
		position: absolute;
		right: 0;
		top: -80px;
		z-index: -1;
		transform: translate(50%, 0); }
	.window1 {
		position: absolute;
		left: 0;
		bottom: -50px;
		z-index: -1;
		transform: translate(-10%, 0); }
	.window2 {
		position: absolute;
		right: 0;
		bottom: -50px;
		z-index: -1;
		transform: translate(30%, 0); }
	.circles3 {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		z-index: -1; }
	.book {
		display: block;
		width: 100%; } }


