.form {
	&-item {}
	&-social {
		margin: 0 0 15px 0;
		@include flex(row, null, null, nowrap);
		.radio {
			&:first-child .radio__btn {
				border-radius: 4px 0 0 4px; }
			&:last-child .radio__btn {
				border-radius: 0 4px 4px 0; } } }
	.radio {
		display: block;
		position: relative;
		cursor: pointer;
		user-select: none;
		&__input {
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			opacity: 0;
			cursor: pointer;
			z-index: 2;
			&:checked ~ .radio__btn {
				background: $blue;
				.radio__text {
					color: white; }
				path {
					fill: white; } } }
		&__btn {
			@include trs;
			background: white;
			@include flex(column, center, center, nowrap);
			width: 90px;
			height: 70px; }
		&__text {
			font-size: 10px;
			margin: 8px 0 0 0; }
		svg {
			display: block;
			height: 24px; }
		path {
			@include trs;
			fill: black; } }
	&__input {
		height: 60px;
		width: 270px;
		background: white;
		border-radius: 4px;
		padding: 0 50px 0 30px;
		font-size: 16px;
		letter-spacing: 0.01em;
		border: 2px solid transparent;
		@include trs;
		&:hover,
		&:focus {
			border: 2px solid $dark; } }
