	// 		background: white
	// 		color: $dark
	.checkbox {
		position: relative;
		user-select: none;
		width: 100%;
		display: block;
		margin: 21px 0 0 0;
		cursor: pointer;
		&__input {
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			opacity: 0;
			cursor: pointer;
			z-index: 2;
			&:checked ~ .checkbox__text {
				&::after {
					opacity: 1; } } }
		&__text {
			font-size: 12px;
			position: relative;
			padding-left: 30px;
			&:hover {
				&:before {
					background: $gray2; } }
			&::before {
				content: '';
				display: block;
				@include trs;
				width: 22px;
				height: 20px;
				border: 1px solid $dark;
				content: '';
				border-radius: 2px;
				position: absolute;
				left: 0;
				top: -5px; }
			&::after {
				content: '';
				display: block;
				width: 18px;
				height: 14px;
				position: absolute;
				left: 3px;
				top: -1px;
				@include trs;
				opacity: 0;
				@include bg(null, null, 'icons/check.svg', no-repeat, center, center, contain); }
			a {
				font-size: 12px;
				color: $dark;
				text-decoration: underline;
				&:hover {
					text-decoration: none; } } } }
	&__text {
		font-size: 11px;
		a {
			font-size: 11px;
			color: $dark;
			text-decoration: underline;
			&:hover {
				text-decoration: none; } } } }


.form-title {
	font-weight: 600;
	font-size: 16px;
	text-transform: uppercase;
	color: white;
	text-shadow: 0px 4px 20px rgba(51, 51, 51, 0.3);
	margin: 0 0 16px 0; }
