.reviews {
	padding: 80px 0 40px;
	.block-title {
		margin: 0 0 36px 0;
		text-align: center; }
	.inner {
		@include flex(row, between, null, wrap); }
	.slider-for {
		max-width: 870px;
		width: 100%;
		.slide {
			border-radius: 4px;
			padding: 30px;
			background: $gray2;
			@include flex(row, between, null, wrap);
			&-left {
				max-width: 340px;
				width: 100%; }
			&-right {
				max-width: 440px;
				width: 100%; }
			&__img {
				display: block;
				width: 100%; }
			&__text {
				font-size: 16px;
				line-height: 1.4;
				font-style: italic;
				&:before,
				&:after {
					content: '';
					display: block;
					width: 100%;
					height: 20px;
					margin: 5px 0; }
				&:before {
					@include bg(null,null,'icons/quote-left.svg',no-repeat,left,center,contain); }
				&:after {
					@include bg(null,null,'icons/quote-right.svg',no-repeat,left,center,contain); } }
			.btn-yellow {
				margin: 34px 0 0 0; } }
		.row {
			margin: 20px 0 0 0;
			@include flex(row, null, null, wrap);
			.block {
				width: 50%; } } }
	.slider-nav {
		max-width: 270px;
		width: 100%;
		.slide {
			// margin: 10px 0
			padding: 20px;
			border-radius: 4px;
			@include trs;
			text-align: center;
			@include flex(column, null, center, wrap);
			&.slick-current {
				background: $gray2; }
			&__img {
				display: block;
				width: 70px;
				border-radius: 100%; }
			&__name {
				margin: 13px 0 0 0;
				text-transform: uppercase;
				font-weight: 700;
				font-size: 16px; }
			&__city {
				margin: 5px 0 0 0;
				font-size: 12px; } } }
	.arrow {
		@include flex(row, center, center, nowrap);
		width: 100%;
		position: static;
		background: none;
		padding: 5px 5px;
		border-radius: 4px;
		@include trs;
		cursor: pointer;
		&:hover {
			background: $yellow; }
		&-prev {
			margin-bottom: 7px; }
		&-next {
			margin-top: 7px; } }
	.user {
		@include flex(row,null,center, wrap);
		&-inner {
			margin: 0 0 0 20px; }
		&__img {
			display: block;
			width: 70px;
			border-radius: 100%; }
		&__name {
			font-weight: 700;
			font-size: 16px;
			text-transform: uppercase; }
		&__text {
			margin: 8px 0 0 0;
			font-size: 12px;
			opacity: .5; } }
	.block {
		width: 100%;
		&.pd {
			margin: 20px 0 15px;
			padding: 20px 0 15px;
			border-top: 1px solid lighten($dark, 65%);
			border-bottom: 1px solid lighten($dark, 65%); }
		&__title {
			letter-spacing: 0.2em;
			font-size: 9px;
			opacity: 0.7;
			text-transform: uppercase;
			margin: 0 0 6px 0; }
		&-list {
			padding-left: 20px;
			li {
				margin: 5px 0;
				list-style-type: disc;
				font-size: 15px;
				&::marker {
					width: 6px;
					color: $yellow; } } } }
	.price {
		font: 800 20px 'Exo', sans-serif; } }


